@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.message{position:relative;font-family:'Raleway', sans-serif;font-size:.95rem;color:#FB6158;width:12rem;font-weight:bold;height:auto;border-radius:5px 5px 5px 0;margin:1.5rem auto;padding:.75rem .75rem 0;background:#182533;word-wrap:break-word}.message__time{display:inline-block;position:relative;left:100%;transform:translateX(-100%);margin:.5rem 0}.message .user-photo{position:absolute;width:2rem;height:2rem;border-radius:50%;bottom:0;left:-2.7rem}.message p{padding-top:.5rem;font-size:.85rem;font-weight:400;color:white}.message span{font-weight:400;position:absolute;right:.65rem;bottom:.65rem;color:white}.message .triangle{position:absolute;border:6px solid transparent;border-right:6px solid #182533;border-bottom:6px solid #182533;left:-12px;bottom:0}.advantages{color:white;background:#182533;border-radius:5px;width:12rem;height:auto;padding:.75rem;margin:1rem auto}.advantages .list{padding:1rem 0 0 0}.advantages__item{background:#0E1621;text-align:center;border-radius:5px;padding:.5rem}.advantages__item+.advantages__item{margin-top:.5rem}.advantages__item--super{color:#61dafb;font-weight:bold}.non-validate{border:2px solid crimson}.names{color:white;display:flex;flex-direction:column;text-align:center;background:#182533;border-radius:5px;width:12rem;height:auto;padding:.75rem;margin:1rem auto}.names__item{padding:.5rem;background:#0E1621;border-radius:.5rem;margin-top:.75rem}.names input{height:2rem;outline:none;padding-left:.5rem;margin:1rem 0}.names button{align-self:center;cursor:pointer;outline:none;border:none;padding:.5rem;font-weight:bold;border-radius:.5rem;width:60%;color:white;background-color:blueviolet}.navbar{position:absolute;left:-12rem;top:2rem;color:white;display:flex;flex-direction:column;text-align:center;background:#182533;border-radius:5px;width:12rem;height:auto;padding:.75rem;-webkit-animation:showMenu 1s ease-out;animation:showMenu 1s ease-out;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.navbar a{color:white;text-decoration:none;padding:.5rem}.navbar a.active{font-weight:bold;font-size:1.1em}@-webkit-keyframes showMenu{from{left:-12rem}to{left:10%}}@keyframes showMenu{from{left:-12rem}to{left:10%}}.error{border-bottom-color:red}.todoList{width:500px;margin:1.5rem auto;color:white}.todoList-newTaskForm input{padding:.15rem;margin:1rem 1rem 0 0}.todoList-task input{margin:.5rem 1rem .5rem 0}.todoList-task select{margin-left:1rem}.todoList-task.done{opacity:0.5}.todoList>div{padding:10px}:root{box-sizing:border-box;font-size:1rem}*{margin:0;padding:0;box-sizing:inherit;font-family:'Raleway', sans-serif}body{overflow-x:hidden}.App{background:#0E1621;display:flex;flex-direction:column;align-items:center;min-height:100vh;width:100vw}.button{outline:none;border:none;background:none;font-family:'Raleway', sans-serif;cursor:pointer;transition:.2s}.button:active{transform:scale(0.95)}.button.filter-active{background-color:blueviolet}.button--todo{border-radius:.5rem;background-color:#fb6158;color:white;padding:.25rem 1rem}.button--todo+.button--todo{margin-left:.5rem}.button--hide{border-radius:.5rem;background-color:dodgerblue;color:white;padding:.25rem 1rem;margin-left:.5rem}.button--delete{border-radius:.5rem;background-color:red;color:white;padding:.25rem 1rem;margin-left:.5rem}

